@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);


body{
    margin: 0px;
    padding: 0px;
}
.App{
    display: flex;
    flex-direction: row;
    max-width: 1000px;
    height: 100vh;
    margin: 0 auto;
    padding: 0px;
    font-family: 'Roboto', sans-serif;
}
.avatar-img{
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin: 10px 10px 0px 10px;
}
.me{
    display: none;
}
.contact{
    display: flex;
    flex-direction: row;
    background-color: #ddd;
    border-left: 1px solid #bbb;
    border-bottom: 1px solid #bbb;
    align-items: center;
    padding-bottom: 10px;
}
.message-input {
  background-color: #ddd;
  border-left: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
  justify-content: center;
  padding: 20px;
}
.message-input-wrapper {
  display: flex;
  border: 1px solid #999;
  border-radius: 15px;
  width: 100%;
  height: 40px;
}
.message-input-field {
  width: 100%;
  border: none;
  border-radius: 15px 0px 0px 15px;
  padding-left: 10px;
  outline: none;
}
.message-img {
  height: 20px;
  padding: 10px;
  -webkit-filter: opacity(25%);
          filter: opacity(25%);
}
.message-button {
  background-color: #fff;
  border: none;
  border-radius: 0px 15px 15px 0px;
  outline: none;
}

.message-wrapper {
  display: flex;
  flex-direction: row;
}
.message {
  display: flex;
  flex-direction: column;
}
.message-text {
  margin-top: 10px;
  padding: 10px;
  color: #fff;
  min-height: 20px;
  background-color: #555;
  border-radius: 20px;
  font-size: 14px;
}
.message-date{
  font-size: 12px;
  color: #999;
  padding: 5px 10px;
}
.me{
  margin-left: auto;
  margin-right: 10px;
}
.me .message-text{
  background-color: #aaa;
  color: #000;
}
.messages {
  background-color: #eee;
  border-left: 1px solid #bbb;
  border-bottom: 1px solid #bbb;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.dialog {
    display: flex;
    flex: 2 1;
    align-content: center;
    flex-direction: column;
}
a {
    text-decoration: none;
    color: #222;
}
.chat-item{
width: 100%;
border-bottom: 2px solid #ddd;
display: flex;
text-decoration: none;
}
.contact-img{
    width: 40px;
    height: 40px;
    border-radius: 20px;
    margin: 10px;
    flex: 1 1;
}
.chat-date{
    margin: 10px;
    font-size: 12px;
    flex: 2 1;
}
.chat-name-message-wrapper {
    margin: 10px;
    flex: 4 1;
}
.chat-message{
    color: #999;
    font-size: 14px;
}
.chat-name{
    font-size: 16px;
}
.chats-title{
    color: aqua;
    margin: 30px 10px;
    font-size: 24px;
}
.search-field{
    display: flex;
    justify-content: start;
    border-radius: 20px;
    border: 1px solid #999;
    height: 30px;
    margin: 10px;
    background-color: #fff;
}
.search-field-input {
   width: 80%;
   border: none;
   outline: none;
}
.search-icon{
    width: 15px;
    height: 15px;
    padding-top: 7px;
    padding-left: 10px;
    -webkit-filter: opacity(25%);
            filter: opacity(25%);
}
.header {
    background-color: #ddd;
}
.sidebar {
    display: flex;
    flex: 1 1;
    justify-content: start;
    flex-direction: column;
}
